<template>
  <div class="special">
    <div class="header">
      <div
        class="item"
        v-for="(item,index) in dataSource.actorrows"
        :key="index"
        @click="itemClick(item)"
      >
        <img class="image" v-lazy="item.avatar" mode="aspectFill">
        <div class="title" v-text="item.spname">></div>
      </div>
    </div>
    <div class="content">
      <list
        v-for="(item,index) in dataSource.rows"
        :info="item"
        :key="index"
        :title="item.spname"
        :dataList="item.vodrows"
      ></list>
      <infinite-loading @infinite="infinite" ref="infiniteLoading"></infinite-loading>
    </div>
  </div>
</template>

<script>
import config from "../js/config.js";
import list from "../components/list.vue";
export default {
  components: {
    list
  },
  data() {
    return {
      dataSource: {},
      page: 0
    };
  },
  computed: {
    v2sliderows() {
      let v2sliderows = [];
      if (this.dataSource.v2sliderows) {
        this.dataSource.v2sliderows.forEach(item => {
          if (item.scene == "special.detail") {
            v2sliderows.push(item);
          }
        });
      }
      return v2sliderows;
    }
  },
  methods: {
    itemClick(item) {
      if (item.spid) {
        this.$router.push({
          path: "./detail?spid=" + item.spid
        });
      }
    },
    infinite($state) {
      console.log("onReachBottom");
      this.loadData(this.page + 1, $state);
    },
    loadData(page, $state) {
      console.log("config", config, config.query());
      this.$axios
        .request({
          url: config.origin + "/special/listing-0-0-" + page,
          params: config.query()
        })
        .then(res => {
          console.log("success", res);
          if (res.status == 200 && res.data.retcode == 0) {
            this.page = page;
            if (this.page == 1) {
              this.dataSource = res.data.data;
            } else {
              this.dataSource.actorrows = res.data.data.actorrows;
              this.dataSource.rows = this.dataSource.rows.concat(
                res.data.data.rows
              );
            }
            if (res.data.data.rows.length < 16) {
              $state && $state.complete();
            } else {
              $state && $state.loaded();
            }
          } else {
            weui.topTips(res.data.errmsg || res.statusCode || "fail");
            $state.loaded();
          }
        })
        .catch(res => {
          if ($state) {
            $state.loaded();
          }
          weui.topTips(res.message || "fail");
        });
    }
  }
};
</script>

<style lang="less" scoped>
.special {
  .header {
    width: 100vw;
    overflow-x: scroll;
    display: flex;

    &::-webkit-scrollbar {
      display: none;
    }

    .item {
      margin: 2px 5px;
      .image {
        width: 60px;
        height: 60px;
        border-radius: 30px;
      }
      .title {
        text-align: center;
        font-size: 12px;
      }
    }
  }
}
</style>
